.operation-message-stud {
  width: 100%;
  background: #F6F7FB;
  height: 100%;
  min-width: 1366px;
}
.operation-message-stud .titleFlex {
  width: 100%;
  position: fixed;
  z-index: 99;
  top: 0;
}
.operation-message-stud .titleFlex .ant-card-body {
  padding: 16px 16px 16px 43px;
}
.operation-message-stud .titleFlex .ant-col-22 {
  text-align: center;
  color: #2A283D;
  font-size: 18px;
  opacity: 0.99;
}
.operation-message-stud .content {
  margin: 93px 30px 30px 44px;
}
.operation-message-stud .content .content-center .ant-card {
  padding: 34px 30px;
  margin-top: 15px;
}
.operation-message-stud .content .content-center .ant-card .ant-card-body {
  padding: 0;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar {
  margin: 0 25px 40px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .ant-col-24 span {
  height: 38px;
  line-height: 38px;
  font-size: 18px;
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions-title {
  margin: 0 34px 10px 34px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions-title .answer-titile {
  margin: 20px 0 10px!important;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer {
  height: 44px;
  line-height: 44px;
  background: #3BB9FF;
  border: 1px solid #1D9FE8;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 10px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer span {
  color: #FFFFFF;
  margin-left: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .other-answer {
  height: 44px;
  line-height: 44px;
  background: #F8FBFC;
  border: 1px solid #D9EEF5;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 5px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .other-answer span {
  color: #2A283D;
  margin-left: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-err {
  height: 44px;
  line-height: 44px;
  margin: 5px 0;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-err span {
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-err img {
  margin-left: 30px;
  vertical-align: -1px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-err span:last-child {
  margin-left: 5px;
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-success {
  height: 44px;
  line-height: 44px;
  margin: 5px 0;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-success span {
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-success img {
  margin-left: 30px;
  vertical-align: -1px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis-success span:last-child {
  margin-left: 5px;
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis {
  padding: 6px 0;
  background: #F7F7F7;
  border: 1px solid #EBEBEB;
  box-sizing: border-box;
  border-radius: 5px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis p {
  margin: 2px 26px 2px 26px;
  font-size: 12px;
  color: #2A283D;
  line-height: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .answer-analysis .analysis {
  margin-top: 12px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer {
  background: #F8FBFC;
  border: 1px solid #D9EEF5;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 24px 0;
  padding: 20px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer p {
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer-analysis {
  padding: 6px 0;
  background: #F7F7F7;
  border: 1px solid #EBEBEB;
  box-sizing: border-box;
  border-radius: 5px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer-analysis p {
  margin: 7px 26px 8px 26px;
  font-size: 12px;
  color: #2A283D;
  line-height: 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer-analysis p span {
  color: #60AA71;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .single-answer-analysis p:first-child {
  margin: 2px 26px 23px 26px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions {
  margin: 10px 0 10px 0;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions .ant-checkbox-wrapper {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions .ant-checkbox-wrapper span:first-child {
  margin-right: 8px;
  background: #3BB9FF;
  border: 1px solid #1D9FE8;
  box-sizing: border-box;
  border-radius: 3px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .questions .ant-checkbox-wrapper span:last-child {
  font-size: 16px;
  color: #2A283D;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-title span {
  font-size: 14px;
  color: #2A283D;
  border-bottom: 1px solid #000000;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling {
  margin: 10px 0 8px 12px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling span:first-child {
  font-size: 14px;
  color: #2A283D;
  margin-right: 20px;
  display: inline-block;
  width: 40px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling span:last-child {
  padding-left: 10px;
  width: 330px;
  display: inline-block;
  background: #F8FBFC;
  border: 1px solid #D9EEF5;
  box-sizing: border-box;
  border-radius: 5px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-answer {
  /*height: 44px;
            line-height: 44px;*/
  margin: 28px 0 20px;
  width: auto;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-answer .sub {
  display: block;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-answer p {
  color: #2A283D;
  margin-left: 12px;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-answer p span {
  width: 125px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.operation-message-stud .content .content-center .ant-card .content-center-mar .gap-filling-answer img {
  margin-left: 30px;
  vertical-align: 2px;
  margin-right: 5px;
}
.operation-message-stud .content .content-center .ant-card .gap-filling-answer-err {
  font-size: 12px;
  color: #E95959;
}
.operation-message-stud .content .content-center .ant-card .gap-filling-answer-succ {
  font-size: 12px;
  color: #60AA71;
}
.operation-message-stud .content .content-center .ant-card .ant-divider {
  margin: 35px 0;
}
.operation-message-stud .content .content-left {
  padding-left: 30px;
  margin-top: 15px;
  position: fixed;
  right: 30px;
}
.operation-message-stud .content .content-left .ant-card-body {
  min-height: 500px;
}
.operation-message-stud .content .content-left .title .ant-col-12:last-child {
  text-align: right;
}
.operation-message-stud .content .content-left .title .ant-col-12:last-child .readOver {
  font-size: 14px;
  color: #2A283D;
  opacity: 0.4;
}
.operation-message-stud .content .content-left .title .ant-col-12:last-child .span-circle {
  border-radius: 50%;
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 0 16px 0 4px;
}
.operation-message-stud .content .content-left .title .ant-col-12:last-child span:last-child {
  margin-right: 0;
}
.operation-message-stud .content .content-left .ant-divider-horizontal {
  margin: 10px 0 0 0;
}
.operation-message-stud .content .content-left .answer-topic {
  margin-top: 15px;
}
.operation-message-stud .content .content-left .answer-topic .span-title {
  color: #2A283D;
  font-size: 12.1553px;
}
.operation-message-stud .content .content-left .answer-topic .circle-col {
  margin-top: 5px;
}
.operation-message-stud .content .content-left .answer-topic .circle-col .circle-topic {
  border-radius: 50%;
  display: inline-block;
  width: 22px;
  height: 22px;
  background: #3BB9FF;
  line-height: 22px;
  color: #FFFFFF;
  text-align: center;
  margin: 5px 7px;
}
html {
  overflow-y: auto;
  overflow-x: auto;
}
